const CompareSnow = (props) => {
  if (props.snow.length === 0) {
    console.log(props.snow.length);
    return <h3 className="snow">Click Compare Snow to Add Resort</h3>;
  } else {
    return (
      <div>
        <h2 className="snow">Snow Comparisons</h2>
        {props.snow.map((resort) => (
          <div key={resort} className="compareResort">
            <h4>{resort} Snow Totals: 24hrs: | 48hrs: </h4>
          </div>
        ))}
      </div>
    );
  }
};

export default CompareSnow;
