import { Component } from "react";
import { resorts } from "./resorts";
import CompareSnow from "./SnowCompare";

class ResortList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      resortCompare: [],
    };
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(name) {
    if (!this.state.resortCompare.includes(name)) {
      this.setState((state) => ({
        resortCompare: [...state.resortCompare, name],
      }));
    }
  }
  render() {
    return (
      <div>
        <div className="resorts">
          {resorts.map((resort) => (
            <div className="resort" key={resort.name}>
              <img src={resort.img} alt="resort" className="image" />
              <h2>{resort.name}</h2>
              <h6>{resort.description}</h6>
              <a href={resort.web} target="_blank" rel="noreferrer">
                <button>Visit Website!</button>
              </a>
              <button onClick={this.handleClick.bind(this, resort.name)}>
                Compare Snow
              </button>
            </div>
          ))}
        </div>
        <CompareSnow snow={this.state.resortCompare} />
      </div>
    );
  }
}

export default ResortList;
